import { NavLink } from "react-router-dom"
import "../css/Navbar.css"


const Navbar=(props)=>{
  

        const verticle_navbar_cls=()=>
        {
                document.querySelector(".verticle-links-div").style.display="none"
        }
        const verticle_navbar_open=()=>
        {
                document.querySelector(".verticle-links-div").style.display="block"
        }
    return(
        <>  
        <div id="navbar">
            <h2>NewsBaba</h2>
            {/**searchbar */}
            {props.path==="/" ?  <div id="search-div">
                <form onSubmit={props.search_event}>
            <input type="search" id="search-input" placeholder="Search Here"/><button ><i class="fa-solid fa-magnifying-glass"></i></button>
            </form>
            </div> : ''}
           
            {/**searchend */}
            <div id="navbar-btn" onClick={verticle_navbar_open}><i class="fa-sharp fa-solid fa-bars"></i></div>
            <div className="horizontal-links-div">
            <NavLink to="/">Home</NavLink>
            <NavLink to="/contact">Contact</NavLink>
            <NavLink to="/about">About</NavLink>
            <NavLink to="/login">Login</NavLink>
            </div>
            </div>
            {/*vericle nav bar */}
            <div className="verticle-links-div">
                <p><span id="close-nav-btn" onClick={verticle_navbar_cls}><i class="fa-sharp fa-solid fa-x"></i></span></p>
                <div className="">
            <NavLink to="/">Home</NavLink>
            <NavLink to="/contact">Contact</NavLink>
            <NavLink to="/about">About</NavLink>
            <NavLink to="/login">Login</NavLink>
            </div>
            </div>
        </>
    )
}

export default Navbar