import { NavLink } from "react-router-dom"
import "../css/login.css"
import Navbar from "./Navbar"
import Footer from "./Footer"
const Login=()=>
{
    return <>
            <Navbar/>
            <div style={{marginTop:"10%"}}>
            <h2>Login Form</h2>
            <form id="login-form">
                <table>
                    <tr>
                        <td>   <label>Email:</label></td>
                        <td><input placeholder="Enter Email" type="email" id="login-email"></input><br/></td>
                    </tr>
                    <tr>
                        <td>  <label>Password</label></td>
                        <td><input placeholder="Enter Password" type="password" id="login-password"></input></td>
                    </tr>
                </table>
                <button>Login</button>
             
                
              
                <p id="dont-hav-account" >I Don't Have Account / <NavLink to="/signup">Create Acount</NavLink></p>
            </form>
            
            </div>
            <Footer/>
    </>
}

export default Login