import Navbar from "./Navbar"
import "../css/about.css"
import Footer from "./Footer"

const About = () => {
    return (<>
        <Navbar />
        
            <div class="about-nested-div">
                <h2>About Us</h2>

                Welcome to our news website, where we strive to provide timely and accurate information to keep you informed about the latest happenings around the world. Our dedicated team of journalists and editors work tirelessly to deliver comprehensive news coverage across a wide range of topics including politics, business, technology, science, entertainment, sports, and more.

                At our news website, we believe in the power of journalism to inform, educate, and empower individuals. Our commitment to journalistic integrity ensures that we uphold the highest standards of reporting, presenting news in a fair, unbiased, and balanced manner. We understand the importance of presenting multiple perspectives and strive to offer a diverse range of opinions and voices in our articles and features.

                As a trusted source of news, we prioritize accuracy and fact-checking. Our team follows rigorous editorial processes to verify information from reliable sources before publishing any news story. We are dedicated to separating fact from fiction and aim to combat the spread of misinformation.

                In addition to delivering breaking news, we also provide in-depth analysis, investigative reports, and thought-provoking features. Our goal is not just to inform, but also to engage and spark meaningful conversations on the pressing issues of our time.

                Our news website is designed to be user-friendly, allowing you to easily navigate through different sections and find the stories that matter to you. We also offer various subscription options, including free access to a limited number of articles, as well as premium subscriptions for those seeking more extensive coverage and exclusive content.

           
        </div>
        <Footer/>

    </>)
}

export default About