import Navbar from "./Navbar"
import "../css/contactus.css"
import Footer from "./Footer"
const Contact=()=>
{
    return(<>
            <Navbar/>
            <div style={{marginTop:"10%"}}>
                <h2>Contact US</h2>
                <form id="contact-form">
                    <table>
                        <tr>
                            <td>     <label for="name">Name:</label></td>
                            <td> <input type="text" placeholder="Enter Name" required id="name" name="name"/><br/></td>
                           
                        </tr>
                        <tr>
                            <td>   <label for="email">Email:</label></td>
                            <td>    <input type="email" placeholder="Enter Contact Email" required id="email" name="email"/></td>
                           
                        </tr>
                        <tr>
                            <td>   <label for="mobile">Mobile</label> </td>
                            <td>     <input type="number" minLength={10} maxLength={10} placeholder="Enter Contact Mobile" required id="mobile" name="mobile"/><br/></td>
                           
                        </tr>
                    </table>
                   
                    
                   
                 
                       
                   
                    <button>Submit</button>
                </form>
            </div>
            <Footer/>
    </>)
}

export default Contact